'use client';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

const menuItems = [
  { 
    name: 'Dashboard', 
    href: '/tms-module/dashboard',
    subItem: 'Overview' 
  },
  { 
    name: 'Car Attendance', 
    href: '/tms-module/car-attendance',
    subItem: 'Daily Records' 
  },
  { 
    name: 'Register New Car', 
    href: '/tms-module/register-car',
    subItem: 'New Vehicle' 
  },
  { 
    name: 'View Car Parking', 
    href: '/tms-module/car-location',
    subItem: 'Locations' 
  },
  { 
    name: 'Assign Service', 
    href: '/tms-module/assign-service',
    subItem: 'Maintenance' 
  },
  { 
    name: 'Assign Car', 
    href: '/tms-module/car-assign',
    subItem: 'Department' 
  },
  { 
    name: 'Complaints', 
    href: '/tms-module/complaints',
    subItem: 'Issues' 
  },
  { 
    name: 'Order Car', 
    href: '/tms-module/order-car',
    subItem: 'Requests' 
  },
];

export default function TMSSidebar() {
  const pathname = usePathname();
  
  return (
    <div className="w-64 bg-white border-r border-gray-200 flex flex-col">
      <div className="p-4 border-b border-gray-200">
        <h1 className="text-xl font-bold text-blue-600">TMS Module</h1>
      </div>
      
      <nav className="flex-1 overflow-y-auto p-2">
        <ul className="space-y-1">
          {menuItems.map((item) => (
            <li key={item.name} className="group">
              <Link 
                href={item.href}
                className={`flex flex-col px-4 py-2 rounded-lg ${
                  pathname === item.href 
                    ? 'bg-blue-50 text-blue-600' 
                    : 'hover:bg-gray-50 text-gray-700'
                }`}
              >
                <span className="font-medium">{item.name}</span>
                <span className={`text-xs mt-1 ${
                  pathname === item.href 
                    ? 'text-blue-500' 
                    : 'text-gray-500'
                }`}>
                  {item.subItem}
                </span>
              </Link>
            </li>
          ))}
        </ul>
      </nav>
      
      <div className="p-4 border-t border-gray-200">
        <div className="text-xs text-gray-500">TMS v2.0</div>
      </div>
    </div>
  );
}